<template>
  <header class="header">
    <div class="container">
      <div class="header__connect">
        <button class="button button--connect">Connected</button>
        <div class="connect-dropdown">
          <ul class="connect-dropdown__ul">
            <li class="connect-dropdown__ul__li">
              <a class="connect-dropdown__ul__li__link" href="#">My creeps</a>
            </li>
            <li class="connect-dropdown__ul__li">
              <a class="connect-dropdown__ul__li__link" href="#">Mint creeps</a>
            </li>
            <li class="connect-dropdown__ul__li">
              <a class="connect-dropdown__ul__li__link" href="#">Disconnect</a>
            </li>
          </ul>
        </div>
      </div>

      <nav class="nav" v-if="!$root.isMobile">
        <a class="nav__item" href="#" @click.prevent="scrollTo('about')"
          >About</a
        >
        <a class="nav__item" href="#" @click.prevent="scrollTo('roadmap')"
          >Roadmap</a
        >
        <a class="nav__item" href="#" @click.prevent="scrollTo('rarity')"
          >Rarity</a
        >
        <a class="nav__item" href="#" @click.prevent="scrollTo('faq')">FAQ</a>
        <a
          class="nav__item nav__item--active"
          href="#"
          @click.prevent="scrollTo('raffle')"
          >Halloween Raffle</a
        >
      </nav>

      <button
        v-if="$root.isMobile"
        class="button button--toggle"
        :class="{ close: !menuOpened }"
        @click="menuToggle()"
      >
        <img
          class="header__burger"
          :class="{ close: menuOpened }"
          src="@/assets/images/burger.svg"
          alt="burger menu"
        />
      </button>

      <transition name="slide-fade">
        <nav class="nav" v-if="$root.isMobile && menuOpened">
          <a class="nav__item" href="#" @click.prevent="scrollTo('about')"
            >About</a
          >
          <a class="nav__item" href="#" @click.prevent="scrollTo('roadmap')"
            >Roadmap</a
          >
          <a class="nav__item" href="#" @click.prevent="scrollTo('rarity')"
            >Rarity</a
          >
          <a class="nav__item" href="#" @click.prevent="scrollTo('faq')">FAQ</a>
          <a
            class="nav__item nav__item--active"
            href="#"
            @click.prevent="scrollTo('raffle')"
            >Halloween Raffle</a
          >

          <div class="logo-with-social__buttons">
            <a
              class="logo-with-social__button"
              :href="item.link"
              v-for="(item, j) in socials"
              :key="j"
            >
              <div :class="['logo-with-social__icon', item.icon]"></div>
            </a>
          </div>
        </nav>
      </transition>

      <div class="logo-with-social">
        <router-link to="/" class="logo-with-social__link">
          <img src="@/assets/images/logo-new.png" />
        </router-link>
        <div class="logo-with-social__buttons" v-if="!$root.isMobile">
          <a
            class="logo-with-social__button"
            :href="item.link"
            v-for="(item, j) in socials"
            :key="j"
          >
            <div :class="['logo-with-social__icon', item.icon]"></div>
          </a>
        </div>
      </div>

      <div class="mint">
        <div class="mint__image-wrapper">
          <img
            class="mint__image"
            src="@/assets/images/bg-mint.png"
            alt="mint opened"
          />
        </div>
        <div class="mint__content">
          <h1 class="mint__title" v-html="title"></h1>
          <button
            class="button button--primary"
            @click="scrollTo('starting-sales')"
            v-html="adoptButton"
          ></button>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: 'Header',
  components: {},
  data() {
    return {
      title: 'Mint will be opened on October 26',
      adoptButton: 'Adopt me',
      menuOpened: false,
      socials: [
        {
          link: '#',
          icon: 'logo-with-social__icon--discord',
        },
        {
          link: '#',
          icon: 'logo-with-social__icon--opensea',
        },
        {
          link: '#',
          icon: 'logo-with-social__icon--twitter',
        },
      ],
    }
  },
  props: {
    isWalletConnected: {
      type: Boolean,
      required: true,
    },
  },
  computed: {
    connectButtonText() {
      return this.isWalletConnected ? 'CONNECTED' : 'CONNECT WALLET'
    },
  },
  mounted() {},
  methods: {
    connectMetaMask() {
      this.$emit('connectMetaMask')
    },
    menuToggle() {
      this.menuOpened = !this.menuOpened
    },
    menuClose() {
      this.menuOpened = false
    },
    scrollTo(id) {
      console.log('id: ', id)
      let el = document.querySelector('#' + id)
      if (el) {
        console.log('el: ', el)
        el.scrollIntoView({
          behavior: 'smooth',
        })
      }
    },
  },

  created() {
    const that = this
    document.addEventListener('keyup', function(evt) {
      if (evt.keyCode === 27) {
        that.menuClose()
      }
    })
  },

  mounted() {
    window.addEventListener('resize', this.menuClose)
    window.addEventListener('scroll', this.menuClose)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.menuClose)
    window.removeEventListener('scroll', this.menuClose)
  },
}
</script>

<style scoped>
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active до версии 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>
