<template>
  <section class="starting-sales">
    <div class="container">
      <div class="starting-sales__wrapper">
        <p class="starting-sales__text">
          We are starting the sale of 10,000 unique collectible NFTs , which are
          also a pass to the NFT Games BMB World.
        </p>
        <div class="starting-sales__countdown-wrapper">
          <div class="countdown" v-if="s_1 != '-'">
            <div class="countdown__item-wrapper">
              <div class="countdown__item-body">
                <span class="countdown__item" id="d_1" :key="'d_1'">{{
                  d_1
                }}</span>
                <span class="countdown__item" id="d_2" :key="'d_2'">{{
                  d_2
                }}</span>
              </div>
              <p class="countdown__label">Days</p>
            </div>

            <div class="countdown__item-wrapper">
              <div class="countdown__item-body">
                <span class="countdown__item" id="h_1" :key="'h_1'">{{
                  h_1
                }}</span>
                <span class="countdown__item" id="h_2" :key="'h_2'">{{
                  h_2
                }}</span>
              </div>
              <p class="countdown__label">Hours</p>
            </div>

            <div class="countdown__item-wrapper">
              <div class="countdown__item-body">
                <span class="countdown__item" id="m_1" :key="'m_1'">{{
                  m_1
                }}</span>
                <span class="countdown__item" id="m_2" :key="'m_2'">{{
                  m_2
                }}</span>
              </div>
              <p class="countdown__label">Hours</p>
            </div>
            <div class="countdown__item-wrapper">
              <div class="countdown__item-body">
                <span class="countdown__item" id="s_1" :key="'s_1'">{{
                  s_1
                }}</span>
                <span class="countdown__item" id="s_2" :key="'s_2'">{{
                  s_2
                }}</span>
              </div>
              <p class="countdown__label">Hours</p>
            </div>

            <button
              class="button button--mint-soon"
              @click.prevent="goTo(startSales)"
            >
              Mint soon
            </button>
          </div>
          <div class="contdown" v-else>
            отсчёт окончен
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Countdown',
  data() {
    return {
      startSales: '#starting-sales',
      d_1: '22',
      d_2: '22',
      h_1: '22',
      h_2: '22',
      m_1: '22',
      m_2: '22',
      s_1: '22',
      s_2: '22',
    }
  },
  methods: {
    async goTo(link) {
      console.log(link)
      if (!this.$scrollTo(link)) {
        // await this.$router.push({ path: '/' })
        setTimeout(() => {
          this.$scrollTo(link)
        }, 500)
      }
    },
    formatCountdown(number, index) {
      let strNumber = number.toString()
      if (strNumber.length == 2) {
        return strNumber[index]
      }
      if (index === 0) {
        return 0
      }
      if (index === 1) {
        return strNumber[0]
      }
    },
    runCountDown() {
      // Set the date we're counting down to
      let countDownDate = new Date('Oct 26 2021 00:01:00').getTime()

      // Update the count down every 1 second
      let x = setInterval(() => {
        // Get today's date and time
        let now = new Date().getTime()

        // Find the distance between now and the count down date
        let distance = countDownDate - now

        // Time calculations for days, hours, minutes and seconds
        let days = Math.floor(distance / (1000 * 60 * 60 * 24))
        let hours = Math.floor(
          (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        )
        let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
        let seconds = Math.floor((distance % (1000 * 60)) / 1000)

        // Display the result in the element with id="demo"
        /*
        document.getElementById("demo").innerHTML = days + "d " + hours + "h "
            + minutes + "m " + seconds + "s ";
         */
        console.log(days, hours, minutes, seconds)

        this.d_1 = this.formatCountdown(days, 0)
        this.d_2 = this.formatCountdown(days, 1)

        this.h_1 = this.formatCountdown(hours, 0)
        this.h_2 = this.formatCountdown(hours, 1)

        this.m_1 = this.formatCountdown(minutes, 0)
        this.m_2 = this.formatCountdown(minutes, 1)

        this.s_1 = this.formatCountdown(seconds, 0)
        this.s_2 = this.formatCountdown(seconds, 1)

        // If the count down is finished, write some text
        if (distance < 0) {
          clearInterval(x)
          //document.getElementById("demo").innerHTML = "EXPIRED";
        }
      }, 1000)
    },
  },
  created() {
    this.runCountDown()
  },
}
</script>

<style></style>
