<template>
  <!-- faq -->
  <section id="faq" class="faq">
    <div class="faq__hat">
      <img src="@/assets/images/hat.png" alt="hat">
    </div>
    <div class="container flex-col">
      <div class="faq__title">
        <h2 class="title rarity__title">FAQ</h2>
      </div>
      <div>
        <div class="faq__ul mt-50">
          <div v-for="(item, i) in faq" :key="i">
            <div class="faq__ul__li">
              <div class="faq__ul__title" @click="toggle(i)">
                <span>{{ item.title }}</span>
                <span
                  class="faq__ul__li__toggle"
                  :class="{ 'i-minus': item.active, 'i-plus': !item.active }"
                ></span>
              </div>
              <slide-up-down :active="item.active" :duration="1000">
                <p class="faq__text">
                  {{ item.text }}
                </p>
              </slide-up-down>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import SlideUpDown from "vue-slide-up-down";
export default {
  name: "Faq",
  components: {
    SlideUpDown,
  },
  data() {
    return {
      faq: [
        {
          title: "What’s an NFT?",
          active: true,
          text: "An NFT stands for “Non-fungible token” and is a fancy way of saying it’s a unique, one of a kind digital item that users can buy, own, and trade. Some NFTs main function are to be digital art and look cool, some offer additional utility like exclusive access to websites or participation in an event, think of it like a rare piece of art that can also act as a “members” card.",
        },
        {
          title: "Where does my NFT go after I purchase a Cool Cat?",
          active: false,
          text: "test text2",
        },
        {
          title: "This sounds awesome, how do I get involved?",
          active: false,
          text: "test text3",
        },
        {
          title: "Soo…Breeding?!",
          active: false,
          text: "test text4",
        },
        {
          title: "What can I do with my Cool Cat?",
          active: false,
          text: "test text5",
        },
      ],
    };
  },
  props: {},
  computed: {},
  mounted() {},
  methods: {
    toggle(i) {
      this.faq[i].active = !this.faq[i].active;
      this.faq.forEach((item, key) => {
        if (key !== i) {
          item.active = false;
        }
      });
    },
  },
};
</script>