<template>
  <section class="bmb-nft-gallery">
    <div class="container">
      <h2 class="bmb-nft-gallery__title">BMB NFT gallery</h2>
    </div>
    <swiper
      class="swiper bmb-nft-gallery__swiper"
      ref="swiper"
      :options="SliderOptions"
    >
      <swiper-slide
        v-for="(picture, i) in pictures"
        :key="i"
        :class="['bmb-nft-gallery__item', picture.class]"
      >
        <img :src="picture.src" :alt="pictures.alt" />
      </swiper-slide>

      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </section>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name: 'BmbNftGallery',
  components: {
    Swiper,
    SwiperSlide,
  },

  data() {
    return {
      SliderOptions: {
        slidesPerView: 'auto',
        loop: true,
        centeredSlides: true,
        loopedSlides: 6,
        autoplay: {
          delay: 1500,
        },

        spaceBetween: 18,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      },
    }
  },
  props: {
    pictures: Array,
  },
}
</script>

